 */

.wp-block-create-block-vayu-blocks {
	background-color: #21759b;
	color: #fff;
	padding: 2px;
}
.block-editor-block-icon.has-colors svg.th-block-icon{
	max-height: 40px;
		max-width: 40px;
		min-height:  40px;
		min-width: 40px;
	   fill:"#000"
	}
.wp-block-vayu-blocks-advance-heading{
	font-size:34px;
}	

.wp-block-vayu-blocks-advance-heading a{
	text-decoration: none;
}


.vayu_blocks_heading_image-heading {
    background-size: 200% 100% !important; /* Extend background horizontally */
    background-clip: text;
    -webkit-background-clip: text !important;
    -webkit-text-fill-color: transparent !important; /* Text needs to be transparent */
}

.vayu_blocks_heading_image_animation-heading{
    animation: headinganimation 8s linear infinite !important;
}

@keyframes headinganimation {
    0% {
        background-position: 0 0;
    }
    100% {
        background-position: 100% 0; /* Move background horizontally */
    }
}

